/* Google Fonts */
*{
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    scroll-padding-top: 2rem;
    scroll-behavior: smooth;
}
/* some variables */
:root{
    --main-color: #ff6c2f;
    --text-color: #020307;
    --bg-color: #fff;
}
/* Handle */
html::-webkit-scrollbar{
   width: 0.5rem !important;
   background:var(--text-color) !important;
}
html::-webkit-scrollbar-thumb{
    background: var(--main-color) !important;
    border-radius: 5rem;
}
  
body{
    background: var(--text-color);
    color: var(--bg-color);
}
section{
    padding:4.5rem 0 1.5rem;
}

header{
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 100px;
    transition: 0.5s;
   /* background:var(--main-color);*/
}

header.shadow{
    background:var(--bg-color)
}

header.shadow .navbar a{
    color:var(--text-color) ;
    box-shadow: 0 0 4px rgb(14 55 54 /15%);
}
header.shadow .logo{
    color:var(--text-color) ;
}

.logo{
    font-size:1.1rem ;
    font-weight:600;
    color: var(--bg-color);
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
}

.logo .bx{
    font-size:24px;
    color: var(--main-color);
}

.navbar{
    display: flex;
    column-gap: 5rem;
}
.navbar li{
    position: relative;
}



.navbar a{
    font-size:1rem ;
    font-weight:500 ;
    color: var(--bg-color);
    text-decoration: none;
}

.navbar a::after{
    content: '';
    width: 0;
    height: 2px ;
    background:var(--main-color);
    position: absolute;
    bottom: -4px;
    left: 0;
    transition: 0.4s all linear;
}
.navbar a:hover:after, .navbar .home-active::after{
    width:100%;
}

#menu-icon{
    font-size:24px;
    cursor: pointer;
    z-index: 1000001;
    display: none;
}

.btn{
    padding:0.7rem 1.4rem;
    background:var(--main-color);
    color: var(--bg-color);
    font-weight: 400;
    border-radius: 0.5rem;
}

.btn:hover{
    background: #fa1216;
}