.heading{
    max-width:968px;
    margin-left: auto;
    margin-right: auto;
    font-size:1.2rem;
    font-weight: 500;
    text-transform:uppercase;
    border-bottom: 1px solid var(--main-color);
}

.heading{
    max-width:85%;
    margin-left: 3%;
    margin-right: 3%;
    font-size:1.2rem;
    font-weight: 500;
    text-transform:uppercase;
    border-bottom: 1px solid var(--main-color);
}


.main{
    position: absolute;
    width: 100%;
    height: 90%;
    overflow-x: auto;
}
.main::-webkit-scrollbar{
    display: none;
}
/* trendingMovies */
.movies{
    width: 95%;
    margin: 1em auto;
    color: rgb(204, 204, 204);
}
.movies .cards{
    margin-top: 0.6em;
    padding: 0.5em 0 2em 0;
    display: flex; 
    overflow-y: auto; 
}
.movies .cards::-webkit-scrollbar{
    display: none;
}
.movies .cards .card{
    flex-shrink: 0;
    width: 250px;
    height: 150px;
    border-radius: 10px;
    cursor: pointer;
    position: relative;
    transition: all .4s;
    margin-right: 1em;
}
.card::before{
    content: attr(data-text);
    position: absolute;
    left: 45%;
    bottom: -15%;   
    transform: translate(-45%);
    font-size: .7em;
    opacity: 0;
    transition: all .4s;
}
.card:hover::before{
    opacity: 1;
}
.card:hover{
    transform: scale(1.05);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.178);
}
.movies .cards .card img{
    width: 100%;
    height: 100%;
    border-radius: 10px; 
}