
.Subscribedata{
    max-width:968px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 2rem;
}

.Subscribedata h2{
    text-align: center;
    font-size:1.2rem;
    font-weight:600 ;
}

.Subscribedata .formdata{
    background: var(--bg-color);
    padding: 10px;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
}

.Subscribedata .formdata .inputdata{
    border: none ;
    outline: none;
}

.Subscribedata .email{
    width: 200px;
}

.Subscribedata .formdata .inputdata::placeholder{
    color: var(--text-color);
    font-size:0.8rem;
    font-weight:400;
}
.Subscribedata .btndata{
    text-transform: uppercase;
    font-weight:500 ;
    cursor: pointer;
}

/*Footer*/
.footerdata {
    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
}

.socialdata{
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
}

.socialdata .bx{
    background: var(--bg-color);
    padding: 2px;
    font-size:2rem;
    border-radius:50%;
    border:4px solid rgb(2,3,7,0.4);
    color: var(--main-color);
}
.socialdata .bx:hover{
    background: var(--main-color);
    color: var(--bg-color);
    transition: 0.2s all linear;
}

.copyright {
    padding: 20px;
    text-align: center;
    color: var(--bg-color);
}